"use client"

import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { useState } from "react"
import { NextMeal } from "@/components/next-meal"

type MealType = 'breakfast' | 'lunch' | 'dinner';

interface UpcomingMeal {
  meal: string;
  time: string;
  mealType: MealType;
  dayIndex: number;
  attending: boolean;
  attendingCount: number;
}

export default function Home() {
  const [guestCount, setGuestCount] = useState(0)

  const getUpcomingMeals = (): UpcomingMeal[] => {
    const now = new Date();
    const currentDay = now.getDay() || 7;
    const currentDayIndex = currentDay - 1;
    const currentHour = now.getHours();
    
    let currentMealType: MealType = 'breakfast';
    let startDayIndex = currentDayIndex;
    
    if (currentHour >= 19) {
      currentMealType = 'breakfast';
      startDayIndex = (currentDayIndex + 1) % 7;
    } else if (currentHour >= 12) {
      currentMealType = 'dinner';
    } else if (currentHour >= 8) {
      currentMealType = 'lunch';
    }

    const upcomingMeals: UpcomingMeal[] = [];
    let nextMealType = currentMealType;
    let nextDayIndex = startDayIndex;
    
    for (let i = 0; i < 3; i++) {
      const mealTime = nextMealType === 'breakfast' ? '8:00 AM' : 
                      nextMealType === 'lunch' ? '12:30 PM' : '7:00 PM';
      
      const mealDate = new Date(now);
      mealDate.setDate(mealDate.getDate() + (nextDayIndex >= currentDayIndex ? 0 : 7) + (nextDayIndex - currentDayIndex));

      const formatDate = (date: Date) => {
        return new Intl.DateTimeFormat('en-US', {
          weekday: 'long',
          month: 'long',
          day: 'numeric'
        }).format(date)
      };

      const attendingCount = weeklyAttendance[nextMealType].reduce((count: number, isAttending: boolean) => 
        isAttending ? count + 1 : count, 0) + (i === 0 ? guestCount : 0);

      upcomingMeals.push({
        meal: `${nextMealType.charAt(0).toUpperCase() + nextMealType.slice(1)}: ${weeklyMeals[nextMealType][nextDayIndex].name}`,
        time: `${formatDate(mealDate)} • ${mealTime}`,
        mealType: nextMealType,
        dayIndex: nextDayIndex,
        attending: weeklyAttendance[nextMealType][nextDayIndex],
        attendingCount
      });

      // Move to next meal/day
      if (nextMealType === 'breakfast') nextMealType = 'lunch';
      else if (nextMealType === 'lunch') nextMealType = 'dinner';
      else {
        nextMealType = 'breakfast';
        nextDayIndex = (nextDayIndex + 1) % 7;
      }
    }
    return upcomingMeals;
  };

  // Weekly meal data
  const weeklyMeals = {
    breakfast: [
      { name: 'Greek Yogurt Bowl', description: 'With fresh berries and honey' },
      { name: 'Avocado Toast', description: 'With poached eggs and cherry tomatoes' },
      { name: 'Oatmeal', description: 'With banana and cinnamon' },
      { name: 'Pancakes', description: 'With maple syrup and fresh fruits' },
      { name: 'Breakfast Burrito', description: 'With eggs, beans, and salsa' },
      { name: 'Continental', description: 'Croissants with jam and butter' },
      { name: 'English Breakfast', description: 'Eggs, bacon, beans, and toast' }
    ],
    lunch: [
      { name: 'Chicken Wrap', description: 'With fresh vegetables and hummus' },
      { name: 'Quinoa Salad', description: 'With roasted vegetables' },
      { name: 'Tuna Sandwich', description: 'With lettuce and mayo' },
      { name: 'Vegetable Soup', description: 'With crusty bread' },
      { name: 'Caesar Salad', description: 'With grilled chicken' },
      { name: 'Pasta Salad', description: 'With pesto and cherry tomatoes' },
      { name: 'Buddha Bowl', description: 'With rice, tofu, and vegetables' }
    ],
    dinner: [
      { name: 'Grilled Salmon', description: 'With asparagus and lemon butter sauce' },
      { name: 'Vegetable Stir Fry', description: 'With tofu and brown rice' },
      { name: 'Chicken Curry', description: 'With basmati rice' },
      { name: 'Beef Tacos', description: 'With guacamole and salsa' },
      { name: 'Mushroom Risotto', description: 'With parmesan cheese' },
      { name: 'Baked Cod', description: 'With roasted potatoes' },
      { name: 'Eggplant Parmesan', description: 'With spaghetti' }
    ]
  };
  type WeeklyAttendance = {
    [key in MealType]: boolean[];
  };

  const [weeklyAttendance, setWeeklyAttendance] = useState<WeeklyAttendance>({
    breakfast: Array(7).fill(false),
    lunch: Array(7).fill(false),
    dinner: Array(7).fill(false)
  });

  const handleAttendance = (attending: boolean) => {
    const now = new Date()
    const currentDay = now.getDay() || 7 // Convert Sunday from 0 to 7
    const dayIndex = currentDay - 1 // Convert to 0-based index
    const currentHour = now.getHours()
    
    let mealType: MealType = 'breakfast'
    if (currentHour >= 19) mealType = 'breakfast'
    else if (currentHour >= 12) mealType = 'dinner'
    else if (currentHour >= 8) mealType = 'lunch'
    
    const mealIndex = currentHour >= 19 ? (dayIndex + 1) % 7 : dayIndex
    
    setWeeklyAttendance(prev => ({
      ...prev,
      [mealType]: prev[mealType].map((value: boolean, i: number) => 
        i === mealIndex ? attending : value
      )
    }));
  };

  const toggleAttendance = (meal: 'breakfast' | 'lunch' | 'dinner', day: number) => {
    setWeeklyAttendance(prev => ({
      ...prev,
      [meal]: prev[meal].map((value, i) => i === day ? !value : value)
    }))
  }

  const setAllAttendance = (value: boolean) => {
    setWeeklyAttendance({
      breakfast: Array(7).fill(value),
      lunch: Array(7).fill(value),
      dinner: Array(7).fill(value)
    })
  }

  return (
    <div className="space-y-8">
      {/* Next Meal Section */}
      <section className="grid md:grid-cols-2 gap-6">
        <NextMeal 
          weeklyMeals={weeklyMeals}
          weeklyAttendance={weeklyAttendance}
          onAttendanceChange={handleAttendance}
          onGuestCountChange={setGuestCount}
        />

        {/* Weekly Attendance Planner */}
        <Card className="p-6">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold">Weekly Attendance Planner</h2>
            <div className="flex items-center space-x-2">
              <button className="p-1 hover:bg-muted rounded text-muted-foreground hover:text-foreground">{'<'}</button>
              <span className="text-sm text-muted-foreground">May 20 - May 26, 2024</span>
              <button className="p-1 hover:bg-muted rounded text-muted-foreground hover:text-foreground">{'>'}</button>
            </div>
          </div>
          <table className="w-full">
            <thead>
              <tr>
                <th className="text-left text-sm font-medium text-muted-foreground">Meal</th>
                <th className="text-right text-sm font-medium text-muted-foreground">Time</th>
                <th className="text-center text-sm font-medium text-muted-foreground px-1">Mon</th>
                <th className="text-center text-sm font-medium text-muted-foreground px-1">Tue</th>
                <th className="text-center text-sm font-medium text-muted-foreground px-1">Wed</th>
                <th className="text-center text-sm font-medium text-muted-foreground px-1">Thu</th>
                <th className="text-center text-sm font-medium text-muted-foreground px-1">Fri</th>
                <th className="text-center text-sm font-medium text-muted-foreground px-1">Sat</th>
                <th className="text-center text-sm font-medium text-muted-foreground px-1">Sun</th>
              </tr>
            </thead>
            <tbody className="divide-y">
              <tr>
                <td className="py-2 text-sm">Breakfast</td>
                <td className="py-2 text-sm text-right">8:00 AM</td>
                {weeklyAttendance.breakfast.map((isAttending, i) => (
                  <td key={i} className="px-1">
                    <Button
                      variant={isAttending ? "default" : "outline"}
                      size="sm"
                      className={`w-full text-xs transition-colors duration-200 ${
                        isAttending 
                          ? "bg-primary hover:bg-primary/90" 
                          : "hover:bg-muted"
                      }`}
                      onClick={() => toggleAttendance('breakfast', i)}
                    >
                      {isAttending ? 'Yes' : 'No'}
                    </Button>
                  </td>
                ))}
              </tr>
              <tr>
                <td className="py-2 text-sm">Lunch</td>
                <td className="py-2 text-sm text-right">12:30 PM</td>
                {weeklyAttendance.lunch.map((isAttending, i) => (
                  <td key={i} className="px-1">
                    <Button
                      variant={isAttending ? "default" : "outline"}
                      size="sm"
                      className={`w-full text-xs transition-colors duration-200 ${
                        isAttending 
                          ? "bg-primary hover:bg-primary/90" 
                          : "hover:bg-muted"
                      }`}
                      onClick={() => toggleAttendance('lunch', i)}
                    >
                      {isAttending ? 'Yes' : 'No'}
                    </Button>
                  </td>
                ))}
              </tr>
              <tr>
                <td className="py-2 text-sm">Dinner</td>
                <td className="py-2 text-sm text-right">7:00 PM</td>
                {weeklyAttendance.dinner.map((isAttending, i) => (
                  <td key={i} className="px-1">
                    <Button
                      variant={isAttending ? "default" : "outline"}
                      size="sm"
                      className={`w-full text-xs transition-colors duration-200 ${
                        isAttending 
                          ? "bg-primary hover:bg-primary/90" 
                          : "hover:bg-muted"
                      }`}
                      onClick={() => toggleAttendance('dinner', i)}
                    >
                      {isAttending ? 'Yes' : 'No'}
                    </Button>
                  </td>
                ))}
              </tr>
            </tbody>
          </table>
          <div className="mt-4 text-sm text-muted-foreground">
            Tip: Set your preferences for the entire week to help with meal planning and reduce food waste.
          </div>
          <div className="mt-4 flex justify-end space-x-2">
            <Button 
              variant="default" 
              className="bg-primary hover:bg-primary/90 transition-colors duration-200 flex items-center space-x-2"
              onClick={() => setAllAttendance(true)}
            >
              <span>Set All to Yes</span>
              <span className="text-xs opacity-75">✓</span>
            </Button>
            <Button 
              variant="outline"
              className="transition-colors duration-200 hover:bg-muted flex items-center space-x-2"
              onClick={() => setAllAttendance(false)}
            >
              <span>Set All to No</span>
              <span className="text-xs opacity-75">✕</span>
            </Button>
          </div>
        </Card>
      </section>

      <MealSchedule 
        weeklyMeals={weeklyMeals}
        weeklyAttendance={weeklyAttendance}
        guestCount={guestCount}
        onToggleAttendance={toggleAttendance}
      />
    </div>
  )
}
